<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showaddclassdialog"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <tree-table
      :expand-type="false"
      :selection-type="false"
      index-text="#"
      show-index
      :data="Catelist"
      :columns="columns"
      border
    >
      <!--  -->
      <template slot="isok" slot-scope="scope">
        <i style="color:green" class="el-icon-success" v-if="scope.row.cat_deleted==false"></i>
        <i style="color:red" class="el-icon-error" v-else></i>
      </template>
      <!--  -->
      <template slot="order" slot-scope="scope">
        <el-tag size="mini" type="primary" v-if="scope.row.cat_level===0"></el-tag>
        <el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1"></el-tag>
        <el-tag size="mini" type="warning" v-else></el-tag>
      </template>
      <!--  -->
      <template slot="opst" slot-scope="scope">
        <el-button type="primary" size="mini" @click="editCate(scope)" icon="el-icon-edit"></el-button>
        <el-button type="danger" size="mini" @click="deleteCate(scope)" icon="el-icon-delete"></el-button>
      </template>
    </tree-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[3, 5, 10, 15]"
      :page-size="queryInfo.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <!--  -->
    <el-dialog
      @close="addCateDialogClosed"
      title=""
      :visible.sync="AddClassdialogVisible"
      width="50%"
    >
      <el-form
        :model="addclassruleForm"
        :rules="addclassruleFormrules"
        ref="addclassruleFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addclassruleForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            v-model="selectKeys"
            :options="paerntCateList"
            :props="{ expandTrigger: 'hover', children:'children',value:'cat_id',label:'cat_name' }"
            size="medium"
            @change="changeCascader()"
            clearable
            change-on-select
            style="width:100%"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AddClassdialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      @close="editdialogClosed"
      :visible.sync="EditCenterDialogVisible"
      width="30%"
      center
    >
      <el-form :model="editRuleForm" :rules="editRules" ref="editRuleFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editRuleForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditCenterDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editCateName"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      Catelist: [],
      // table
      columns: [
        {
          label: "",
          prop: "cat_name"
        },
        {
          label: "",
          type: "template",
          template: "isok"
        },
        {
          label: "",
          type: "template",
          template: "order"
        },
        {
          label: "",
          type: "template",
          template: "opst"
        }
      ],
      // 
      AddClassdialogVisible: false,
      // 
      addclassruleForm: {
        cat_name: "",
        cat_pid: 0,
        cat_level: 0
      },
      // 
      addclassruleFormrules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      paerntCateList: [],
      // 
      selectKeys: [],
      // 
      EditCenterDialogVisible: false,
      editRuleForm: {
        cat_name: ""
      },
      editRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 1  10 ", trigger: "blur" }
        ]
      },
      editId: 0
    };
  },
  created() {
    this.getCatelist();
  },
  methods: {
    // 
    async getCatelist() {
      const { data: res } = await this.$http.get(`categories`, {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("!");
      }
      console.log(res.data);
      this.total = res.data.total;
      this.Catelist = res.data.result;
    },
    // 
    handleSizeChange(newValue) {
      this.queryInfo.pagesize = newValue;
      this.getCatelist();
    },
    // 
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage;
      this.getCatelist();
    },
    showaddclassdialog() {
      // 
      this.getparentCateList();
      this.AddClassdialogVisible = true;
    },
    async getparentCateList() {
      const { data: res } = await this.$http.get(`categories`, {
        params: { type: 2 }
      });
      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      console.log(res.data);
      this.paerntCateList = res.data;
    },
    // 
    changeCascader() {
      if (this.selectKeys.length > 0) {
        this.addclassruleForm.cat_pid = this.selectKeys[
          this.selectKeys.length - 1
        ];
        this.addclassruleForm.cat_level = this.selectKeys.length;
      } else {
        this.addclassruleForm.cat_pid = 0;
        this.addclassruleForm.cat_level = 0;
      }
    },
    addCate() {
      this.$refs.addclassruleFormRef.validate(async valid => {
        if (!valid) {
          return;
        }
        const { data: res } = await this.$http.post(
          `categories`,
          this.addclassruleForm
        );
        if (res.meta.status !== 201) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.getCatelist();
        this.AddClassdialogVisible = false;
      });
    },
    // 
    addCateDialogClosed() {
      this.$refs.addclassruleFormRef.resetFields();
      this.addclassruleForm.cat_level = 0;
      this.addclassruleForm.cat_pid = 0;
      this.selectKeys = [];
    },
    // 
    deleteCate(scopeData) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            `categories/${scopeData.row.cat_id}`
          );
          if (res.meta.status !== 200) {
            return $message.error("");
          }
          this.$message({
            type: "success",
            message: "!"
          });
          this.getCatelist();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
   async editCate(spocedData) {
      this.EditCenterDialogVisible = true;
      this.editId = spocedData.row.cat_id;
      const { data: res } = await this.$http.get(`categories/${this.editId}`)
      if (res.meta.status !== 200) {
       return this.$message.error("")
      }
      this.editRuleForm.cat_name = res.data.cat_name
    },
    async editCateName() {
      const { data: res } = await this.$http.put(
        `categories/${this.editId}`,
        this.editRuleForm
      );
      console.log(res);

      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.EditCenterDialogVisible = false;
      this.getCatelist();
    },
    editdialogClosed() {
      this.editRuleForm.cat_name = "";
    }
  }
};
</script>

<style lang="less" scpoed>
</style>